/**
 * @func throttle
 * @desc üìù ÂáΩÊï∞ËäÇÊµÅÔºåÊØèÈöî‰∏ÄÊÆµÊó∂Èó¥ÊâßË°å‰∏ÄÊ¨°ÔºåÈò≤Ê≠¢ÂáΩÊï∞Ëøá‰∫éÈ¢ëÁπÅË∞ÉÁî®ÔºåÂØºËá¥ÊÄßËÉΩÈóÆÈ¢ò
 * @param {Function} fn
 * @param {number} [ms=1000]
 * @returns {Function}
 */
export const throttle = (fn: Function, ms: number = 1000): Function => {
  let isRunning = false;
  return (...args: any[]) => {
    if (isRunning) return;
    isRunning = true;
    setTimeout(() => {
      fn(...args);
      isRunning = false;
    }, ms);
  }
}